* {
  margin: 0;
  padding: 0;
  font-family: "Nunito Sans";
  color: white;
  text-decoration: none;
}

html {
  background-color: #191919;
}

.App {
  display: flex;
  padding: 1cm;
  justify-content: space-between;
  align-items: flex-start;
  gap: .5cm;
  height: calc(100vh - 2cm);
  overflow: auto;
  // position: relative;


  // &::before{
  //   position: absolute;
  //   content: "";
  //   inset: 0;
  //   background-image: url("back.jpg");
  //   background-position: center;
  //   background-repeat: no-repeat;
  //   background-size: cover;
  // }

}

.AppRight {
  // border: 1px solid white;
  width: 100%;
  height: 100%;
}

.AppLeft {
  // border: 1px solid white;
  width: 10cm;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.setting-box {
  display: flex;
  justify-content: flex-end;
}

.setting-img {
  width: .8cm;
  height: .8cm;
  padding: .15cm;
  border-radius: 10px;

  // border: 1px solid white;
  background-color: #2a2a2a;

  cursor: pointer;

  &:hover {
    .setting {
      opacity: 1;
    }
  }

}

.setting {
  width: 100%;
  height: 100%;
  transition: .25s all ease;
  opacity: .7;
}

.time-text {
  color: rgb(238, 238, 238);
  font-size: 50px;
  font-family: "Nunito Sans";
  font-weight: 600;
}

.day-name {
  color: white;
}

.fav-box {
  display: flex;
  gap: .6cm;
}

.fav {
  // border: 1px solid white;
  width: 1.7cm;
  height: auto;
  display: flex;

  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: .15cm;
  cursor: pointer;

  &:hover {
    .fav-img {
      opacity: 1;
    }
  }


}

.fav-img-box {
  border-radius: 10px;
  width: 1.7cm;
  height: 1.7cm;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: .1cm;
  background-color: #2a2a2a;
}

.fav-img {
  width: 60%;
  height: 60%;
  opacity: .7;
  transition: .25s all ease;
}

.pi{
  width: 45%;
  height: auto;
}

.fav-name {
  font-size: 13px;
  text-align: center;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  -webkit-line-clamp: 1;
  /* Limiting to four lines */
  text-overflow: ellipsis;
}

.period {
  font-size: 20px;
  margin-left: .07cm;
}

.city-input-box {
  display: flex;
  margin-top: .2cm;
  align-items: center;
  gap: .2cm;
}

.city-name {
  padding: .15cm;
  border-radius: 5px;
  background-color: transparent;
  border: 1px solid rgb(86, 86, 86);
  color: white;
}

.check {
  width: .6cm;
  border-radius: 5px;
  cursor: pointer;
  border: 1px solid rgb(86, 86, 86);
  padding: .1cm;
}

.time-box {
  padding: .7cm 1.2cm;
  background-color: #2a2a2a;
  // background-color: transparent;
  /* margin-top: 2cm; */
  backdrop-filter: blur(20px);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.temp-line {
  display: flex;
  margin-top: .2cm;
  gap: .3cm;
  align-items: center;
}